import React from "react";
import "../assest/css/funnyvideo.scss";
import store from "../store";
import Like from '../assest/image/1_like1.png'

class FunnyVideo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items1: [],
      items2: [],
      num1: 1,
      num2: 2,
    };
  }
  // 防抖
  debounce(fn, wait) {
    var timeout = null;
    return function () {
      if (timeout !== null) clearTimeout(timeout);
      timeout = setTimeout(fn, wait);
    };
  }
  // 处理函数
  handle() {
    var box = document.getElementsByClassName("funnyvideo")[0];
    var top1 = document.getElementById("box1").offsetHeight;
    var top2 = document.getElementById("box2").offsetHeight;
    var topmin = Math.min(top1, top2);
    var topall = parseInt(box.scrollTop + window.innerHeight);
    if (topall > topmin) {
      this.setState({
        num1: this.state.num1 + 2,
        num2: this.state.num2 + 2,
      });
      this.getData1(this.state.num1);
      this.getData2(this.state.num2);
    }
  }
  getData1(i) {
    React.axios
      .get("https://api.apiopen.top/getJoke?page=" + i + "&count=10&type=video")
      .then((res) => {
        this.state.items1.push(...res.data.result);
        this.setState({
          items1: this.state.items1,
        });
      });
  }
  getData2(i) {
    React.axios
      .get("https://api.apiopen.top/getJoke?page=" + i + "&count=10&type=video")
      .then((res) => {
        this.state.items2.push(...res.data.result);
        this.setState({
          items2: this.state.items2,
        });
      });
  }
  componentDidMount() {
    document
      .getElementsByClassName("funnyvideo")[0]
      .addEventListener("scroll", this.debounce(this.handle.bind(this), 200));
    this.getData1(this.state.num1);
    this.getData2(this.state.num2);
  }
  show(item){
    store.dispatch({type:'show',payload:{num:1,item}})
  }
  render() {
    return (
      <div>
        <div className="funnyvideo">
          <ul className="box box1" id="box1">
            {this.state.items1.map((item, index) => (
              <li key={index} onClick={()=>this.show(item)}>
                <img src={item.thumbnail} alt="" className="videoimg" />
                <span className="headerimg">
                  <img src={item.header} alt="" />
                </span>
                <span className="time"><img src={Like} alt=""/>{item.up}</span>
              </li>
            ))}
          </ul>
          <ul className="box box2" id="box2">
            {this.state.items2.map((item, index) => (
              <li key={index} onClick={()=>this.show(item)}>
                <img src={item.thumbnail} alt="" className="videoimg" />
                <span className="headerimg">
                  <img src={item.header} alt="" />
                </span>
                <span className="time"><img src={Like} alt=""/>{item.up}</span>
              </li>
            ))}
          </ul>
        </div>
      </div>
    );
  }
}
export default FunnyVideo;
