import React from "react";
import "../assest/css/navlist.scss";
import { Tabs } from "antd-mobile";
import FunnyVideo from "./FunnyVideo";
import FunnyText from "./FunnyText";
import FunnyGif from "./FunnyGif";
import FunnyImg from "./FunnyImg";
import video from "../assest/image/video.png";
import text from "../assest/image/text.png";
import gif from "../assest/image/gif.png";
import image from "../assest/image/image.png";
import pic from "../assest/image/pic.jpg";

const tabs2 = [
  { title: <img src={video} className="navimg" alt="" />, sub: "1" },
  { title: <img src={image} className="navimg" alt="" />, sub: "2" },
  { title: <img src={gif} className="navimg" alt="" />, sub: "3" },
  { title: <img src={text} className="navimg" alt="" />, sub: "4" },
];

class NavList extends React.Component {
  constructor(props){
    super(props)
    this.state={
      num:0
    }
  }
  downLoad(i) {
    this.setState({
      num:i
    })
  }
  render() {
    return (
      <div className="navlist">
        {
          this.state.num===1?<div className='download' onClick={()=>this.downLoad(0)}>下载App</div>:''
        }
        <div className="logo">
          <span className="_logo">LOGO</span>
          <span className="other">
            <span className="seach">
              <img src={pic} alt="" />
            </span>
            <span className="app" onClick={() => this.downLoad(1)}>
              下载App
            </span>
          </span>
        </div>
        <Tabs
          tabs={tabs2}
          initialPage={0}
          tabBarPosition="top"
          renderTab={(tab) => <span className="navtitle">{tab.title}</span>}
        >
          <div className="tab">
            <FunnyVideo />
          </div>
          <div className="tab">
            <FunnyImg />
          </div>
          <div className="tab">
            <FunnyGif />
          </div>
          <div className="tab">
            <FunnyText />
          </div>
        </Tabs>
      </div>
    );
  }
}
export default NavList;
