import React from "react";
import "../assest/css/funnytext.scss";
import { Card, WingBlank, WhiteSpace } from "antd-mobile";

class FunnyText extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lists: [],
      num: 1,
    };
  }
  // 防抖
  debounce(fn, wait) {
    var timeout = null;
    return function () {
      if (timeout !== null) clearTimeout(timeout);
      timeout = setTimeout(fn, wait);
    };
  }
  // 处理函数
  handle() {
    var box = document.getElementsByClassName("funnytext")[0];
    var top = document.getElementById("top").offsetHeight;
    var topall = parseInt(box.scrollTop + window.innerHeight);
    if (topall > top) {
      this.setState({
        num: this.state.num + 1,
      });
      this.getData(this.state.num);
    }
  }
  componentDidMount() {
    document
      .getElementsByClassName("funnytext")[0]
      .addEventListener("scroll", this.debounce(this.handle.bind(this), 200));
    this.getData(this.state.num);
  }
  getData(i) {
    React.axios
      .get("https://v1.alapi.cn/api/joke?page=" + i + "&num=10")
      .then((res) => {
        this.state.lists.push(...res.data.data.data);
        this.setState({
          lists: this.state.lists,
        });
      });
  }
  render() {
    return (
      <div className="funnytext">
        <ul className="lists" id="top">
          {this.state.lists.map((item, index) => (
            <li className="list" key={index}>
              <WingBlank size="lg">
                <WhiteSpace size="lg" />
                <Card>
                  <Card.Header title={item.title} />
                  <Card.Body>
                    <div
                      className="content"
                      dangerouslySetInnerHTML={{ __html: item.content }}
                    ></div>
                  </Card.Body>
                  <Card.Footer
                    extra={<div className="time">{item.date}</div>}
                  />
                </Card>
                <WhiteSpace size="lg" />
              </WingBlank>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}
export default FunnyText;
