import React from "react";
import "../assest/css/funnygif.scss";
import { Card, WingBlank, WhiteSpace } from "antd-mobile";

class FunnyGif extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lists: [],
      num: 1,
    };
  }
  // 防抖
  debounce(fn, wait) {
    var timeout = null;
    return function () {
      if (timeout !== null) clearTimeout(timeout);
      timeout = setTimeout(fn, wait);
    };
  }
  // 处理函数
  handle() {
    var box = document.getElementsByClassName("funnygif")[0];
    var top = document.getElementById("top3").offsetHeight;
    var topall = parseInt(box.scrollTop + window.innerHeight);
    if (topall > top) {
      this.setState({
        num: this.state.num + 1,
      });
      this.getData(this.state.num);
    }
  }
  getData(i) {
    React.axios
      .get("https://api.apiopen.top/getJoke?page=" + i + "&count=10&type=gif")
      .then((res) => {
        this.state.lists.push(...res.data.result);
        this.setState({
          lists: this.state.lists,
        });
      });
  }
  componentDidMount() {
    document
      .getElementsByClassName("funnygif")[0]
      .addEventListener("scroll", this.debounce(this.handle.bind(this), 200));
    this.getData(this.state.num);
  }
  doGif(item, e) {
    e.target.src = item.images;
  }
  render() {
    return (
      <div className="funnygif">
        <ul className="lists" id="top3">
          {this.state.lists.map((item, index) => (
            <li className="list" key={index}>
              <WingBlank size="lg">
                <WhiteSpace size="lg" />
                <Card>
                  <Card.Header title={item.name} thumb={item.header} />
                  <Card.Body>
                    <div className="content">{item.text}</div>
                  </Card.Body>
                  <img
                    src={item.thumbnail}
                    alt=""
                    className="bigimg"
                    onClick={(e) => this.doGif(item, e)}
                  />
                  <Card.Footer
                    extra={<div className="time">{item.passtime}</div>}
                  />
                </Card>
                <WhiteSpace size="lg" />
              </WingBlank>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}
export default FunnyGif;
