import React from "react";
import "../assest/css/video.scss";
import { NavBar, Icon } from "antd-mobile";
import store from "../store";
import "../assest/font/iconfont.css";

class Video2 extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: 0,
      item: null,
      num: 0,
    };
  }
  show() {
    this.setState({
      show: store.getState().num,
      item: store.getState().item,
    });
  }
  hide() {
    this.setState({
      show: 0,
    });
  }
  componentDidMount() {
    store.subscribe(this.show.bind(this));
  }
  downLoad(i) {
    this.setState({
      num: i,
    });
    var x=document.getElementsByClassName('_video')[0]
    if(i===1){
      x.pause()
    }else{
      x.play()
    }
  }
  render() {
    return (
      <div>
        {this.state.num === 1 ? (
          <div className="download" onClick={() => this.downLoad(0)}>
            下载App
          </div>
        ) : (
          ""
        )}
        {this.state.show === 1 ? (
          <div className="video">
            <NavBar
              mode="light"
              icon={<Icon type="left" />}
              onLeftClick={() => this.hide()}
              className="navbar"
            >
              {this.state.item.name}
              <span className="userimg">
                <img src={this.state.item.header} alt="" />
              </span>
            </NavBar>
            <div className="videoBox">
              <video src={this.state.item.video} controls autoPlay className='_video'></video>
              <div className="content">
                <p className="toApp" onClick={() => this.downLoad(1)}>
                  App 内打开，流畅有高清
                </p>
                <p className="title">
                  <span className="hot">
                    <span className="iconfont icon-hot"></span> 热门
                  </span>
                  <span className="title_text">{this.state.item.text}</span>
                </p>
                <p className="detail">
                  <span className="name">
                    <span className="up">UP</span>
                    {this.state.item.name}
                  </span>
                </p>
                <p className="other">
                  <span className="iconfont icon-zan" onClick={() => this.downLoad(1)}></span>{" "}
                  {this.state.item.up}
                  <span className="iconfont icon-cai-copy" onClick={() => this.downLoad(1)}></span>{" "}
                  {this.state.item.down}
                  <span className="iconfont icon-zhuanfa" onClick={() => this.downLoad(1)}></span>{" "}
                  {this.state.item.forward}
                  <span className="time">{this.state.item.passtime}</span>
                </p>
                <div className="talk">
                  <div className="talkimg">
                    <img src={this.state.item.top_comments_header} alt="" />
                  </div>
                  <div className="talktxt">
                    <p className="name">{this.state.item.top_comments_name}</p>
                    <p className="txt">
                      {this.state.item.top_comments_content}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
    );
  }
}
export default Video2;
